@mixin flex($justifyContent, $flexDirection, $alignItems) {
  display: flex;
  justify-content: $justifyContent;
  flex-direction: $flexDirection;
  align-items: $alignItems; }

.flexCenterRow {
  @include flex(center, row, center); }

.flexCenterColumn {
  @include flex(center, column, center); }

.panelHomeButtonMain {
  margin: 1% 0 1% 0;
  padding: 0 15px 0 15px; }

.panelHomeButtonMainFlex {
  @include flex(space-around, row, center);
  margin: 15px 0 0 0;
  padding: 15px; }

.panelHomeExchange {
  @include flex(space-between, row, center);
  margin: 5px 20px 5px 10px; }

.panelHomeSpan {
  color: white;
  background-color: black;
  border-radius: 5px;
  padding: 3px; }

.panelHomeBalance {
  @include flex(center, row, center);
  margin: 0 0 5px 0;
  padding: 0; }

.panelHomePanelHeaderSimple {
  @include flex(center, row, center);
  margin: 0 0 5px 0;
  padding: 0; }

@mixin panelHomePanelHeaderSimpleButton($backgroundColor, $color) {
  @include flex(center, column, center);
  font-size: 10px;
  width: 40px;
  height: 20px;
  background-color: $backgroundColor;
  color: $color;
  border-radius: 5px;
  user-select: none;
  margin-left: 5px; }

.panelHomePanelHeaderSimpleButton.bright_light {
  @include panelHomePanelHeaderSimpleButton(#010101, white); }

.panelHomePanelHeaderSimpleButton.space_gray {
  @include panelHomePanelHeaderSimpleButton(white, #010101); }


.balancePornCoin {
  font-size: 30px;
  font-weight: bold;
  font-family: "Roboto",sans-serif;
  color: #E48900;
  margin: 0;
  padding: 0; }


.cpsPornCoin {
  font-size: 15px;
  font-weight: bold;
  font-family: "Roboto",sans-serif;
  color: green;
  margin: 0;
  padding: 0; }

.rating {
  font-size: 30px;
  font-weight: bold;
  font-family: "Roboto",sans-serif;
  color: #FFB800;
  margin-right: 5px;
  padding: 0; }


.panelHomeButton {
  margin: 1% 0 0 0;
  padding: 0 15px 0 15px; }

.spinner {
  display: flex;
  alignItems: center;
  flexDirection: column; }

@mixin panelHeader {
  display: flex;
  align-items: start;
  justify-content: center;
  flex-direction: column;
  margin: 5px 0 0 15px;
  font-size: 10px; }


.panelHeader.bright_light {
  @include panelHeader;
  color: black; }

.panelHeader.space_gray {
  @include panelHeader;
  color: white; }



